function rand(array) {
    return array[Math.floor(Math.random() * array.length)]
}

export function generateName(race, gender) {

    switch (race) {
        case "Argonian":
            if (gender === "Male") {
                if (Math.random() > 0.5) {
                    let x = Math.random();
                    if (x > 0.70) {
                        return rand(argonianMaleNames);
                    } else if (x > 0.10) {
                        return rand(argonianMaleNames) + "-" + rand(argonianMaleNames);
                    } else {
                        return rand(argonianMaleNames) + "-" + rand(argonianMaleNames) + "-" + rand(argonianMaleNames);
                    }
                }
                return rand(argonianTamrielicStarts) + "-" + rand(argonianMaleTamrielicMiddles) + "-" + rand(argonianTamrielicEnds);
            } else {
                if (Math.random() > 0.5) {
                    let x = Math.random();
                    if (x > 0.70) {
                        return rand(argonianFemaleNames);
                    } else if (x > 0.10) {
                        return rand(argonianFemaleNames) + "-" + rand(argonianFemaleNames);
                    } else {
                        return rand(argonianFemaleNames) + "-" + rand(argonianFemaleNames) + "-" + rand(argonianFemaleNames);
                    }
                }
                return rand(argonianTamrielicStarts) + "-" + rand(argonianFemaleTamrielicMiddles) + "-" + rand(argonianTamrielicEnds);
            }
        case "Breton":
            if (gender === "Male") {
                return rand(bretonMaleFirstNames) + " " + rand(bretonSurnames);
            }
            return rand(bretonFemaleFirstNames) + " " + rand(bretonSurnames);
        case "Dark Elf":
            if (gender === "Male") {
                return rand(darkElfMaleFirstNames) + " " + rand(darkElfLastNames);
            }
            return rand(darkElfFemaleFirstNames) + " " + rand(darkElfLastNames);
        case "High Elf":
            if (gender === "Male") {
                return rand(highElfMaleNames);
            }
            return rand(highElfFemaleNames);
        case "Imperial":
            if (gender === "Male") {
                return rand(imperialMaleFirstNames) + " " + rand(imperialLastNames);
            }
            return rand(imperialFemaleFirstNames) + " " + rand(imperialLastNames);
        case "Khajiit":
            if (gender == "Male") {
                if (Math.random() > 0.5) {
                    return rand(khajiitMaleHonorifics) + "'" + rand(khajiitMaleNames).toLowerCase();
                }
                return capitalise(rand(khajiitMaleNames));
            }
            if (Math.random() > 0.5) {
                return rand(khajiitFemaleHonorifics) + "'" + rand(khajiitFemaleNames).toLowerCase();
            }
            return capitalise(rand(khajiitFemaleNames));
        case "Nord":
            if (gender === "Male") {
                if (Math.random() > 0.4) {
                    return rand(nordMaleFirstNames) + " " + rand(nordLastNames);
                }
                return rand(nordMaleFirstNames);
            }
            if (Math.random() > 0.4) {
                return rand(nordFemaleFirstNames) + " " + rand(nordLastNames);
            }
            return rand(nordFemaleFirstNames);
        case "Orc":
            if (gender === "Male") {
                return rand(orcMaleFirstNames) + " Gro-" + rand(orcLastNames);
            }
            return rand(orcFemaleFirstNames) + " Gra-" + rand(orcLastNames);
        case "Redguard":
            if (gender === "Male") {
                return rand(redguardMaleNames);
            }
            return rand(redguardFemaleNames);
        case "Wood Elf":
            if (gender === "Male") {
                return rand(woodElfMaleNames);
            }
            return rand(woodElfFemaleNames);
        default:
            return "Greebeedeebee Peeplepoop";
    }

}

function capitalise(string) {
    return string.charAt(0).toUpperCase() + string.slice(1);
}